import React, { useEffect, useState} from "react";
import { useDispatch, useSelector } from "react-redux";
import { connectM } from "./redux/blockchain/blockchainActions";
import { connect, isConnected } from "./redux/stakeBlockchain/blockchainActions";
import { fetchData, balance, rewards} from "./redux/stakeData/dataActions";
import {fetchDataM, hasApproved } from "./redux/data/dataActions2";
import WalletsModal from "./components/modal/walletsOptionModal";
import styled from "styled-components";

import "./styles/stake.css";

export const StyledButton = styled.button`
  padding: 10px;
  border-radius: 50px;
  border: none;
  background: linear-gradient(to bottom, #673ab7 0%, #cc00ff 100%);
  padding: 10px;
  font-weight: bold;
  color: var(--secondary-text);
  width: 100px;
  cursor: pointer;
  ${'' /* box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3); */}
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

const Grid = styled.div`
  display: grid;
  gap: 10px;
  width: 100%;
  justify-self: center;
  justify-content: center;
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;



const Staking = () => {
    const dispatch = useDispatch();
    const blockchain = useSelector((state) => state.blockchain);
    const data = useSelector((state) => state.data);
    const [feedback, setFeedback] = useState("STAKE");
    const [claiming, setClaiming] = useState(false)
    const [staking, setStaking] = useState(false);
    const [unstaking, setUnstaking] = useState(false);
    const [hasStaked, setHasStaked] = useState(false);
    const [_stake, _unStake] = useState("STAKE");
    const account = blockchain.account;
    const [tokenId, setTokenId] = useState([]);
    const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });
 //   useEffect(()=>{
    //    dispatch(WalletsModal())
        
  //  },[])

  //  useEffect(()=>{
   //     setTimeout(() => {
   //         hasApproved ? dispatch(connect()) : dispatch(WalletsModal())
  //      }, 3000);
//    },[])
    const getConfig = async () => {
    const configResponse = await fetch("/stakeConfig/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
        SET_CONFIG(config);
    };

    const getDataM = () => {
        if (account !== "" && blockchain.smartContract !== null) {
            dispatch(fetchDataM())
        }
    };
    const getData = () => {
        if (account !== "" && blockchain.smartContract !== null) {
            dispatch(fetchData())
        }
    };
    useEffect(() => {
        getConfig();
    }, []);
    useEffect(() => {
        getDataM();
        getData();
    }, [account]);

    const stake = (tokenId) => {
        setStaking(true);
        setFeedback("BUSY")
        dispatch(connect())
        blockchain.smartContract.methods.stakeByIds([tokenId])
            .send({
                to: CONFIG.CONTRACT_ADDRESS,
                from: account
            })
            .once("error", (err) => {
                console.log(err)
                setStaking(false)
            })
            .then((Response)=>{
                setStaking(false)
            })
        
        setFeedback("STAKE")   
    }


    const unStake = async (tokenId) => {
        setUnstaking(true)
        dispatch(connect())
        blockchain.smartContract.methods.unstakeByIds([tokenId])
            .send({
                to: CONFIG.CONTRACT_ADDRESS,
                from: account
            })
            .once("error", (err) => {
                console.log(err)
                setUnstaking(false)
            })
            .then((Response)=>{
                setUnstaking(false)
            })
    }

    const unstakeAll = () => {
        setUnstaking(true)
        dispatch(connect())
        blockchain.smartContract.methods.unstakeAll()
            .send({
                to: CONFIG.CONTRACT_ADDRESS,
                from: account
            })
            .once("error", (err) => {
                console.log(err)
                setUnstaking(false)
            })
            .then((Response)=>{
                setUnstaking(false)
            })                
    }
    
    const approve = async () => {
       // dispatch(WalletsModal());
       dispatch(connect());
        await blockchain.smartContract.methods
        .setApprovalForAll(CONFIG.CONTRACT_ADDRESS, true)
        .send({
            to: CONFIG.CONTRACT_ADDRESS,
            from: account
        })
        dispatch(connect())
    }

    const claim = async (tokenId) => {
        setClaiming(true)
        dispatch(connect())
        await blockchain.smartContract.methods.claimAll()
        .send({
            to: CONFIG.CONTRACT_ADDRESS,
            from: account
        })
            .once("error", (err) => {
                console.log(err)
                setClaiming(false)
            })
            .then((Response)=>{
                setClaiming(false)
            })
    }
    const handleChange = (e) =>{
        e.preventDefault();
        setTokenId(parseInt(e.target.value.split(" ")));
    }

    return ( 
        <main className="stake-app">
            <h1 style={{color: "#ff5722"}}>STAKE</h1>
            <h2>Stake Your CroLicker Club To Earn $LICK Tokens</h2>
            <Grid>
                      {/* connect wallet modal button */}
                      <StyledButton>
                        <WalletsModal />
                      </StyledButton>
                    </Grid>
            <h2>
                {staking ? "staking.." : ""}
                {unstaking ? "unstaking.." : ""}
                {claiming ? "claiming.." : ""}
            </h2>

            {/* <button 
                className="connect-btn"
                onClick={(e)=>{
                    !isConnected ?
                    dispatch(WalletsModal())
                    : e.preventDefault()
                } }
            >
                {!isConnected ? "CONNECT WALLET" : account.slice(0, 10)}
            </button> */}
            <div className="card-container">
                <div>
                    <div>
                        <p>Stake CroLicker Club NFT</p>
                        <p>Earn $LICK</p>
                    </div>
                    <blockquote className="reward"><span>BALANCE</span>
                    {' '}
                    {balance / 10 ** 18}
                    {' '}$LICK
                    </blockquote>
                     <blockquote className="reward"><span>REWARDS</span>
                    {' '}
                    {rewards / 10 ** 18}
                    {' '}$LICK
                    </blockquote>
                    <form className="form" onChange={handleChange}>
                       <input
                            type="all"
                            placeholder="Enter TokenId"
                            className=""
                            required
                        />


                        <button 
                            className="btn stake-btn" 
                            onClick={hasApproved ? (e) => {
                                e.preventDefault()
                                stake(tokenId)
                                } : 
                                (e) => {
                                e.preventDefault()
                                approve(tokenId)
                                }}
                                >
                            {hasApproved ? "STAKE" : "APPROVE"}
                        </button>
                    </form>
                    <form className="form" onChange={handleChange}>
                        <input
                            type="all"
                            placeholder="Enter TokenId"
                            className=""
                            required
                        />
                        <button 
                            className="btn stake-btn" 
                            onClick={(e)=> {
                                e.preventDefault()
                                unStake(tokenId)
                            }}>
                            UNSTAKE
                        </button>
                    </form>
                    <div>
                        <button 
                            className="btn"
                            onClick={claim}>
                            CLAIM LICK
                        </button>
                    </div>
                    <div className="">
                        <button 
                            className="btn mb-5"
                            onClick={unstakeAll}>
                            UNSTAKE ALL
                        </button>
                    </div>
                </div>
                
            </div>
        </main>
    );
}
export default Staking ;
