import { HashLink as Link } from "react-router-hash-link";
import { useDispatch, useSelector } from "react-redux";
import { useState } from "react";
//import { connectM } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import WalletsModal from "./components/modal/walletsOptionModal";

import "./styles/navbar.css";
const Navbar = () => {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  getData();

  // OPEN SIDEBAR
  const [openNav, setOpenNav] = useState(false);

  return (
    <>
      <nav className="nav-wrap">
        <Link className="logo" to="/">
          <img
            src="https://i.ibb.co/z60BGTG/logo192.png"
            width="63"
            height="60"
          />
        </Link>
        <ul  className={openNav ? "active" : ""}>
          <li
            className="mintbox"
            //  onClick={(e) => {
            //  e.preventDefault();
            //  dispatch(WalletsModal());
            // }}
            onClick={() => setOpenNav(false)}
          >
            <Link to="/">HOME</Link>
          </li>
          <li className="stakebox" onClick={() => setOpenNav(false)}>
            <Link to="/staking">STAKE</Link>
          </li>
          <a href="https://corgistudio.io/collection/63067a1f3d543d849a74ab2e" className="button3" onClick={() => setOpenNav(false)}>
            MINT
          </a>
          <a href="https://babyphenix.gitbook.io/babyphenix/" className="button3" onClick={() => setOpenNav(false)}>
            DOCS
          </a>
        </ul>

        <div className="hamburger" onClick={() => setOpenNav(!openNav)}>
          <input className="checkbox" type="checkbox" onClick={() => setOpenNav(!openNav)}/>
          <div
            className={openNav ? "hamburger_lines active" : "hamburger_lines"}
            onClick={() => setOpenNav(!openNav)}
          >
            <span className="line line1" onClick={() => setOpenNav(!openNav)}></span>
            <span className="line line2" onClick={() => setOpenNav(!openNav)}></span>
            <span className="line line3" onClick={() => setOpenNav(!openNav)}></span>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
