import React from 'react'
import { HashLink as Link } from 'react-router-hash-link'
import { useDispatch, useSelector } from "react-redux";
import { connectM } from "./redux/blockchain/blockchainActions";
import { fetchDataM } from "./redux/data/dataActions";
import "./image/hero.gif";
import './styles/home.css';
import { Image } from "./data"

const home = () => {
  return (
    <div className="homeContainer">
    <div className="intro">
    <p className="h01">Baby PHNX</p>
    </div>
    <div className="hero">
    <div className="svgcont">
        <img src="https://i.ibb.co/XSL0b4m/IMG-20220805-174637-209.png" alt="" className="heroimg" />
    </div>
    <div className="herotext">

    <p className="h02">Stake your BABYPHENIX NFT to earn BABYPHENIX TOKENS</p>
    <div className="text">
    <p></p>
    <p>Stake your BabyPhenix NFT to earn $BPHENIX tokens join the $BPHENIX rewards holders</p>

    </div>
    <a href="https://mm.finance/swap?outputCurrency=0xd316da9a1021788e75692ffac7992e86ff95fb2b" className="button2">Buy $BPHENIX</a>
    </div>
    </div>
    <div>
    <h1 className="container">Baby PHNX</h1>
        <div className="carousel">
        <div className="image-list">
                {Image.map ((item) => (
                <img key={item.id} src={item.img} className="caroimg" />
                ))}
            </div>
        </div>
    </div>
    <div className="link">
    <Link className="stake" to="/staking">Stake</Link>
    <Link className="mint" to="/mint">Mint</Link>
    </div>
    <div className="footer">Made with  <img src="https://i.ibb.co/44ZjM2B/pngwing-com.png" alt="pngwing-com" className="love"/> By CroLick Team</div>
        {/* <li className="mintbox" onClick={(e) => {
                    e.preventDefault();
                    dispatch(connectM());
                }}>
                <Link to="/mint">MINT</Link>
                </li> */}
    </div>
  )
}

export default home